.header {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
  }
  
  .photo {
    flex: 1;
    background-size: cover;
    background-position: center;
    margin: 0;
    position: relative;
  
    &:before {
      content: "";
      display: block;
      padding-top: 100%; /* Set the aspect ratio (square: 100%) */
    }
  }
  
  .photo1 {
    background-image: url('../../../public/img1.png');
  }
  
  .photo2 {
    background-image: url('../../../public/img2.png');
  }
  
  .photo3 {
    background-image: url('../../../public/img3.png');
  }
  
  .color-box {
    flex: 1;
    position: relative;
  
    &:before {
      content: "";
      display: block;
      padding-top: 100%; /* Set the aspect ratio (square: 100%) */
    }
  }
  
  .darkblue {
    background-color: #213355;
  }
  
  .orange {
    background-color: #ff5d21;
  }
  
  @media (max-width: 768px) {
    .photo {
      width: 100%; /* Adjust width for mobile version */
    }
  
    .photo:last-child {
      display: none;
    }
  }
  